import React from 'react';
import { List, InputItem, WhiteSpace, TextareaItem, Button, WingBlank, Modal, Toast } from 'antd-mobile';
import { createForm } from 'rc-form';

class rxForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          modalSucess: false,
          modalError: false,
        };
      }
    onClose = key => () => {
        this.setState({
          [key]: false,
        });
      }
    showModal = key => (e) => {
        e.preventDefault();
        this.setState({
          [key]: true,
        });
      }
    submit = (e) => {
        this.props.form.validateFields((error, value) => {
            console.log(error, value);
            if (error) {
                const result = [].concat.apply(...Object.keys(error).map((key) => {
                    return [...error[key].errors];
                  }));
                Toast.info(result.map((elem) => {
                    return elem.message;
                }).join(''));
            }
            else {
                this.showModal('modalSucess')(e);
                this.props.form.resetFields();
            }});
    }
    render() {
        const { getFieldProps } = this.props.form;
        return (
          <WingBlank>
            <WhiteSpace />
            <List renderHeader={() => ''}>
            <InputItem
              clear
              placeholder=""
              {...getFieldProps('name', {
                rules: [
                    {
                    required: true,
                    message: '',
                    },
                ],
                validateTrigger: null,
                })}
              ><span style={{ color: 'red' }}>*</span></InputItem>
              <InputItem
                {...getFieldProps('id')}
                type="bankCard"
                clear
                placeholder="8888 8888 8888"
              ></InputItem>
              <InputItem
                {...getFieldProps('phone', {
                    rules: [
                        {
                        required: true,
                        message: '',
                        },
                    ],
                })}
                type="phone"
                clear
                placeholder="186 1234 1234"
              ><span style={{ color: 'red' }}>*</span></InputItem>
            </List>
            <WhiteSpace />
            <List renderHeader={() => ''}>
            <TextareaItem
                {...getFieldProps('note')}
                placeholder=""
                autoHeight
                labelNumber={5}
                rows={7}
            />
            </List>
            <WhiteSpace />
            <Button className="btn" type="primary"  onClick={this.submit}></Button>
            <Modal
                title=""
                transparent
                maskClosable={false}
                visible={this.state.modalSucess}
                onClose={this.onClose('modalSucess')}
                footer={[{ text: '', onPress: () => { this.onClose('modalSucess')(); } }]}
            >
                <br />
            </Modal>
            <Modal
                title=""
                transparent
                maskClosable={false}
                visible={this.state.modalError}
                onClose={this.onClose('modalError')}
                footer={[{ text: '', onPress: () => { this.onClose('modalError')(); } }]}
            >
                <br />
            </Modal>
        </WingBlank>
        );
    }
}

const rcForm = createForm()(rxForm);

export default rcForm;


